define('wt/views/change-password', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].View.extend({
    templateName: 'change-password',

    keyPress: function keyPress(e) {
      'use strict';
      if (e.keyCode === 13) {
        this.get('controller').send('change');
      }
    }

  });

});