define('wt/views/weights/edit', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].View.extend({
    templateName: 'weights/edit',

    keyPress: function keyPress(e) {
      'use strict';

      if (e.keyCode === 13) {
        this.get('controller').send('save');
      }
    }

  });

});